@import "./partials/normalize";
@import "./partials/variables";

* {
    padding: 0;
    margin: 0;
    list-style-type: none;
    box-sizing: border-box;
}

body {
    font-family: "Helvetica", Arial, sans-serif;
    width: 100%;
    height: 100%;
    overflow-x: hidden;
}

img {
    max-width: 100%;
}
.container {
    margin: 0 auto;
    max-width: 100%;
    padding: 0 15px;
    height: 100%; }

.header {
    background: $main;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.13);
    &__row {
        padding: 17px 30px;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &__left {
        display: flex;
        align-items: center;
    }
    &__logo {
        border-right: 1px solid #222138;
        padding-right: 25px;
        margin-right: 20px;
    }
    &__menu {
        position: relative;
        &.active .header__widgets {
            opacity: 1;
            visibility: visible;
        }
    }
    &__widgets {
        width: 333px;
        height: 423px;
        box-shadow: 0 6px 17px rgba(0, 0, 0, 0.33);
        border-radius: 2px 0 1px 2px;
        background-color: #3c3b5e;
        position: absolute;
        z-index: 2;
        opacity: 0;
        visibility: hidden;
        transition: $transition;
        padding: 50px 40px 20px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        top: 50px;
        &:before {
            content: '';
            border-bottom: 10px solid #3c3b5e;
            border-top: 10px solid transparent;
            border-left: 10px solid transparent;
            border-right: 10px solid #3c3b5e;
            position: absolute;
            transform: rotate(-45deg);
            left: -10px;
            top: -10px;
        }
    }
}
.notification {
    display: flex;
    align-items: center;
    &__item {
        margin-right: 50px;
        position: relative;
        &:last-of-type {
            margin-right: 0;
            border-left: 1px solid #222138;
            padding-left: 20px;
        }
    }
    &__link {
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
        text-decoration: none;
        &:after {
            content: attr(data-num);
            width: 14px;
            height: 13px;
            background-color: #ff5a60;
            border-radius: 50%;
            font: 10px $bold;
            color: $white;
            text-decoration: none;
            display: flex;
            justify-content: center;
            align-items: center;
            right: -7px;
            top: 9px;
            position: absolute;
        }
        &--profile {
            width: 47px;
            height: 47px;
            background-color: #4c4a80;
            border-radius: 50%;
            color: $white;
            font: 16px $medium;
            color: $white;
            text-decoration: none;
            text-align: center;
            &:after {
                display: none;
            }
        }
    }
}
.signIn__modal {
    box-shadow: 0 6px 17px rgba(0, 0, 0, 0.33);
    border-radius: 2px 0 1px 2px;
    background-color: #3c3b5e;
    width: 260px;
    position: absolute;
    right: 0;
    top: 60px;
    opacity: 0;
    visibility: hidden;
    transition: $transition;
    z-index: 1;
    &.active {
        opacity: 1;
        visibility: visible;
    }
    &:after {
        content: '';
        border-bottom: 10px solid #3c3b5e;
        border-top: 10px solid transparent;
        border-left: 10px solid transparent;
        border-right: 10px solid #3c3b5e;
        position: absolute;
        -webkit-transform: rotate(-45deg);
        -ms-transform: rotate(-45deg);
        transform: rotate(-135deg);
        right: 10px;
        top: -5px;
    }
}
.signIn__account {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #56547c;
    padding: 15px;
}
.signIn__name {
    font: 22px $medium;
    color: $white;
    margin-bottom: 5px;
    letter-spacing: 0.61px;
}
.signIn__email {
    font: 14px $regular;
    color: rgba(255,255,255,.7);
    letter-spacing: 0.39px;
}
.signIn__exit {
    border: none;
    outline: none;
    background: none;
    cursor: pointer;
}
.add__account {
    padding: 15px;
    outline: none;
    border: none;
    background: none;
    cursor: pointer;
    display: flex;
    align-items: center;
    letter-spacing: 0.47px;
    height: 80px;
    font: 17px $medium;
    color: $white;
    img {
        margin-right: 10px;
    }
}
.tools {
    background-color: #272841;
    &__row {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        justify-content: flex-start;
        padding: 20px 25px;
    }
    &__item {
        position: relative;
        &:hover .tools__tooltip {
            opacity: 1;
        }
    }
    &__toolip {
        font: 14px $regular;
        color: $white;
        background: rgba(0,0,0,.7);
        position: absolute;
        left: 0;
        top: 25px;
        padding: 5px 15px;
        z-index: 1;
        border-radius: 3px;
        opacity: 0;
        transition: $transition;
        &:before {
            content: '';
            left: 50%;
            top: -5px;
            background: rgba(0,0,0,.7);
            width: 7px;
            height: 7px;
            transform: rotate(45deg);
            position: absolute;
        }
    }
    &__list {
        display: flex;
        align-items: center;
        .tools__item {
            margin-right: 20px;
            cursor: pointer;
            &:last-of-type {
                padding-right: 20px;
                border-right: 1px solid #605e94;
            }
        }
    }
    &__more {
        cursor: pointer;
        border: none;
        outline: none;
        background: none;
        position: relative;
        right: 0;
        transition: $transition;
        &:hover {
            right: 5px;
        }
    }
    &__run {
        width: 133px;
        height: 31px;
        border-radius: 2px;
        background: url(../img/triangle.svg) no-repeat 10px 50%,#6abf0d;
        border: none;
        padding-left: 20px;
        outline: none;
        cursor: pointer;
        font: 16px $demi;
        color: $white;
    }
    &__select {
        width: 90px;
        height: 31px;
        box-shadow: 0 3px 8px rgba(0, 0, 0, 0.16);
        border-radius: 2px;
        border: 1px solid #5b588a;
        background: url(../img/arr.svg) no-repeat  95% 50%, #3d3b60;
        font: 14px $medium;
        color: $white;
        padding-left: 5px;
        padding-right: 5px;
        outline: none;
        -webkit-appearance: none;
        -moz-appearance: none;
        appearance: none;
    }
}
.sidebar {
    box-shadow: 2px 4px 8px rgba(0, 0, 0, 0.13);
    background-color: #2a2d41;
    width: 230px;
    height: 100%;
    position: relative;
    z-index: 1;
    transition: $transition;
    &:before {
        opacity: 0;
        transition: $transition;
    }
    &.active {
        width: 60px;
        &:before {
            content: 'File Manager';
            font: 16px $medium;
            color: $white;
            position: absolute;
            left: 50%;
            writing-mode: vertical-lr;
            top: 50%;
            transform: translate(-50%,-50%);
            opacity: 1;
        }
        .hide__btn {
            transform: rotate(180deg)
        }
        .hide__panel {
            text-align: center;
        }
        .files__list {
            display: none;
            opacity: 0;
            visibility: hidden;
        }
    }
}
.hide__panel {
    text-align: right;
    padding: 15px;
    transition: $transition;
}
.hide__btn {
    background: none;
    cursor: pointer;
    outline: none;
    transition: $transition;
    border: none;
}

.files__project {
    display: none;
}
.files__list {
    position: relative;
    div {
        cursor: pointer;
        position: relative;
    }
    &.active {
        .files__project {
            display: block;
        }
    }
    &__main {
        color: $white;
        font: 14px $medium;
        padding: 10px;
        cursor: pointer;
        &:before {
            content: '';
            width: 10px;
            height: 10px;
            background: url(../img/arrow_yellow.svg) no-repeat center;
            display: inline-block;
            margin-right: 5px;
            transition: $transition;
            transform: rotate(90deg);
        }
        &.active {
            &:before {
                transform: rotate(0deg);
            }
        }
    }
    &__sub {
        font: 14px $regular;
        color: rgba(255,255,255,.9);
        padding: 5px 20px 5px 15px;
        transition: $transition;
        img {
            margin: 0 5px 0 0;
        }
        &:before {
            content: '';
            width: 10px;
            height: 10px;
            background: url(../img/arrow_blue.svg) no-repeat center;
            display: inline-block;
            transition: $transition;
        }
        &.active {
            &:before {
                transform: rotate(90deg);
            }
            & > .files__list__inside {
                display: block;
            }
        }
        &:hover {
            background-color: #383c56;
        }
    }
    &__inside {
        font: 14px $regular;
        color: rgba(255,255,255,.9);
        padding-left: 30px;
        padding-top: 10px;
        display: none;
    }
    &__inner {
        margin-bottom: 5px;
    }
}
.main {
    display: flex;
    background: #1e1e30;
    height: calc(100vh - 152px);
}
.content {
    flex: 1;
    display: flex;
    flex-direction: column;
}
.contents__tabs {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.contents__tab {
    flex: 1;
    background-color: #292d43;
    height: 38px;
    padding: 0 30px;
    display: flex;
    align-items: center;
    font: 16px $demi;
    color: #7876ad;
    opacity: 0.5;
    justify-content: space-between;
    transition: $transition;
    &:hover , &.active {
        opacity: 1;
        color: $white;
        .delete__tab {
            opacity: 1;
        }
    }
}
.delete__tab {
    border: none;
    outline: none;
    cursor: pointer;
    background: none;
    opacity: 0.5;
}

.widgets__list {
    display: flex;
    flex-wrap: wrap;
}
.widget__item {
    flex-basis: 33%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font: 13px $regular;
    color: $white;
    margin-bottom: 25px;
}
.all__widgets {
    height: 46px;
    border-radius: 2px;
    background-color: #1b9eba;
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
    text-transform: uppercase;
    text-decoration: none;
    font: 17px $bold;
    color: $white;
}
.widget__name {
    margin-top: 10px;
}
.status__bar {
    height: 30px;
    border-radius: 1px;
    border: 1px solid #43456a;
    background-color: #353757;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 10px;
}
.status__left {
    display: flex;
}
.pages {
    color: rgba(255,255,255,.9);
    font: 16px $medium;
    letter-spacing: 0.17px;
    margin-right: 30px;
}
.words {
    color: rgba(255,255,255,.5);
    font: 12px $medium;
    letter-spacing: 0.04px;
    display: flex;
    align-items: flex-start;
    img {
        margin: 0 5px;
    }
}
.status__right {
    display: flex;
    align-items: center;
    button {
        outline: none;
        cursor: pointer;
        background: none;
        border: none;
        margin: 0 5px;
        cursor: pointer;
    }

}
.scale__info {
    width: 55px;
    height: 22px;
    border-radius: 2px;
    background-color: #454872;
    letter-spacing: 0.06px;
    font: 15px $medium;
    color: rgba(255,255,255,.9);
    display: flex;
    justify-content: center;
    align-items: center;
}
.content__frame {
    flex: 1;
    height: 100%;
    position: relative;
    overflow: hidden;
}

.inner__tools {
    height: 51px;
    background-color: #2d2c48;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 0 40px 0 25px;
    position: absolute;
    width: 100%;
    top: 0;
}
.inner__tools .tools__list {
    padding: 0;
    margin: 0;
    list-style-type: none;
    display: flex;
    align-items: center;
}
.inner__tools .tools__list .tools__item {
    margin-right: 25px;
    cursor: pointer;
}
.inner__tools .tools__list .tools__item:last-of-type {
    padding-right: 25px;
    border-right: 1px solid #605e94;
}
.save {
    color: #0dc1e8;
    letter-spacing: 0.72px;
    font: 17px $medium;
    border: none;
    outline: none;
    cursor: pointer;
    background: none;
}
.custom__dialog {
    background-color: #302f4c;
    width: 230px;
    height: 100%;
    position: absolute;
    right: 0;
    top: 51px;
    z-index: 1;
    overflow: auto;
    padding-bottom: 50px;
}
.custom__dialog__name {
    font: 17px $demi;
    color: rgba(255,255,255,.9);
    letter-spacing: 0.5px;
    height: 39px;
    border-radius: 1px;
    padding: 0 13px 0 7px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #42476c;
    button {
        border: none;
        cursor: pointer;
        outline: none;
        background: none;
    }
}
.custom__dialog__icons {
    height: 24px;
    background-color: #383c56;
    width: 100%;
    display: flex;
    align-items: center;
    padding: 0 14px;
    li {
        margin-right: 12px;
        cursor: pointer;
    }
}
iframe {
    border: none;
    outline: none;
}
.custom__dialog__misc {
    padding: 20px 13px;
    &.active {
        .custom__misc__item {
            display: flex;
        }
    }
    li {
        position: relative;
        letter-spacing: 0.47px;
        display: none;
        font: 16px $regular;
        color: #fff;
        padding-left: 13px;
        padding-bottom: 13px;
        border-left: 1px solid #fff;
        &:last-of-type {
            padding-bottom: 0;
        }
        &:before {
            content: '';
            width: 6px;
            height: 1px;
            border-radius: 1px;
            background-color: #ffffff;
            position: absolute;
            left: 0;
            top: 9px;
            display: inline-block;
        }
        span:first-of-type {
            width: 115px;
            overflow: hidden;
            text-overflow: ellipsis;
        }
    }
    .custom__misc__main {
        cursor: pointer;
        display: flex;
        &.active::before {
            content: '-';
        }
    }
    .custom__misc__main:before {
        content: '+';
        width: 11px;
        height: 11px;
        border: 1px solid #fff;
        border-radius: 2px;
        top: 0;
        left: -6px;
        display: flex;
        justify-content: center;
        background: no-repeat;
        align-items: center;
        z-index: 1;
        background: #302f4c;
    }
}

.overlay {
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    z-index: 10;
    background: rgba(0,0,0,.5);
    transition: all .3s linear;
    opacity: 0;
    visibility: hidden;
    &.active {
        opacity: 1;
        visibility: visible;
    }
}
.modal {
    position: fixed;
    left: 50%;
    top: 50%;
    box-shadow: 0 6px 17px rgba(0, 0, 0, 0.33);
    border-radius: 2px;
    background-color: #3c3b5e;
    max-width: 420px;
    width: 100%;
    z-index: 11;
    transform: translate(-50%,-50%);
    transition: all .3s linear;
    opacity: 0;
    visibility: hidden;
    &.active {
        opacity: 1;
        visibility: visible;
    }
    &__top {
        border-radius: 2px;
        background-color: #48476a;
        padding: 16px 16px 19px 16px;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    &__name {
        letter-spacing: 0.65px;
        font: 22px $demi;
        color: rgba(255,255,255,.9);
    }
    &__form {
        padding: 30px 48px 20px;
        &__item {
            margin-bottom: 15px;
            display: flex;
            flex-direction: column;
            &:last-of-type {
                margin-bottom: 40px;
            }
        }
        label {
            letter-spacing: 0.25px;
            font: 12px $medium;
            color: $white;
            margin-bottom: 9px;
        }
        input , select {
            height: 35px;
            border-radius: 4px;
            border: 1px solid #4e4d76;
            background-color: #383758;
            outline: none;
            padding-left: 7px;
            padding-right: 7px;
            appearance: none;
            letter-spacing: 0.33px;
            font: 16px $regular;
            color: rgba(255,255,255,.5);
            &::placeholder {
                font: 16px $regular;
                color: rgba(255,255,255,.5);
            }
        }
        select {
            background: url(../img/select.svg) no-repeat 95% 50% , #383758;
        }
    }
}
.closeModal {
    outline: none;
    border: none;
    background: none;
    cursor: pointer;
}
.send {
    width: 250px;
    height: 46px;
    border-radius: 2px;
    background-color: #1b9eba;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    align-items: center;
    outline: none;
    border: none;
    cursor: pointer;
    text-transform: uppercase;
    font: 17px $bold;
    color: $white;
}
.lvl__name {
    letter-spacing: 0.45px;
    font: 16px $medium;
    color: #fff;
    margin-bottom: 10px;
}
.lvl__list {
    display: flex;
    align-items: center;
    li {
        transition: $transition;
        width: 55px;
        border: 1px solid #4e4d76;
        background-color: #383758;
        display: flex;
        height: 36px;
        align-items: center;
        justify-content: center;
        cursor: pointer;
        letter-spacing: 0.33px;
        font: 16px $medium;
        color: rgba(255,255,255,.5);
        &:hover, &.active {
            background-color: #188197;
            color: $white;
        }
        &:first-of-type {
            border-radius: 4px 0 0px 4px;
        }
        &:last-of-type {
            border-radius: 0 4px 4px 0px;
        }
    }
}
.date {
    &::-webkit-calendar-picker-indicator {
        background: url(../img/calendar1.svg) no-repeat 95% 50%;
        appearance: none;
    }
    &::-webkit-inner-spin-button {
        display: none;
        appearance: none;
    }
}
.toolbox {
    background-color: #25283d;
    width: 160px;
    height: 100%;
    position: absolute;
    left: 0;
    top: 51px;
    &__top {
        border-radius: 1px;
        background-color: #292c41;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 6px 10px 6px 20px;
        letter-spacing: 0.67px;
        font: 16px $medium;
        color: rgba(255,255,255,.5);
    }
    &__close {
        border: none;
        outline: none;
        cursor: pointer;
        background: none;
    }
    &__search {
        width: 152px;
        height: 30px;
        margin: 8px auto 15px;
        input {
            border-radius: 4px;
            border: 1px solid #444368;
            background: url(../img/search.svg) no-repeat 95% 50% , #25283d;
            letter-spacing: 0.29px;
            font: 14px $regular;
            color: rgba(255,255,255,.5);
            padding-left: 7px;
            padding-right: 10px;
            width: 100%;
            height: 100%;
            &::placeholder {
                letter-spacing: 0.29px;
                font: 14px $regular;
                color: rgba(255,255,255,.5);
            }
        }
    }
}
